.users-list {
  display: inline-grid;
  grid-template-columns: repeat(3, auto);
  margin: 0 12px 0 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-right: -12px;
  }

  .image-wrapper {
    align-self: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid $color-black-100;

    img,
    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
