.logo {
  display: inline-block;
  width: 101px;
  height: 30px;
  color: $color-default-white;

  &:not(span) {
    transition: opacity $trans-default;

    @include hover-focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.8;
    }
  }

  svg {
    width: 100%;
    height: 100%;
  }
}
