.header {
  z-index: $z-index-header;
  font-family: $ff-chakra-petch;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px;
  padding: 29px 0;
  transition: background-color $trans-default;

  @include vp-1023 {
    padding: 39px 0;
  }

  &--sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }

  &--base {
    background-color: $color-transparent;
  }

  &--blur {
    &:not(.is-open) {
      background-color: rgba($color-jaguar, 0.8);
      backdrop-filter: blur(5px);
      transform: translate3d(0, 0, 0);
      will-change: transform;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__logo {
    display: flex;
    color: $color-default-white;
    margin-right: 44px;
  }
}
