.advantages-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 50px;
  margin: 0;
  padding: 42px 65px 53px 65px;
  width: 100%;
  list-style: none;
  color: $color-spun-pearl;
  border: 1px solid;
  background: $color-jaguar;
  border-image: $gray-to-black;
  border-image-slice: 1;

  @include vp-1023 {
    flex-direction: column;
    align-items: center;
    padding: 36px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 242px;
    max-width: 322px;
    height: 100%;

    @include vp-1023 {
      flex-basis: auto;
      width: 100%;
      max-width: none;
    }
  }

  &__icon-wrapper {
    width: 70px;
    height: 70px;
    margin-bottom: 42px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;

    .title {
      color: $color-default-white;
      margin-bottom: 12px;
    }
  }

  &__description {
    font-family: $ff-chakra-petch;
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;

    p {
      margin: 0 0 24px 0;

      &:only-child,
      &:last-child {
        margin: 0;
      }
    }
  }
}
