.background-page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  pointer-events: none;

  &__image-top {
    position: absolute;
    top: 0;
    right: 0;

    @include vp-767 {
      top: -79px;
      right: 0;
      width: 435px;
      height: 367px;
    }

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__image-grid {
    position: absolute;
    top: 284px;
    left: -792px;
    width: 2682px;
    height: 721px;

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__image-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 1011px;
    height: 498px;

    @include vp-767 {
      width: 637px;
      height: 314px;
    }

    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--grid-bottom {
    .background-page__image-grid {
      top: 881px;
      left: -1074px;

      @include vp-767 {
        top: 942px;
        left: -1063px;
      }
    }
  }

  &--no-grid {
    .background-page__image-grid {
      display: none;
    }
  }
}
