.main-nav {
  flex-grow: 1;

  @include vp-1023 {
    flex-grow: 0;
  }

  &__wrapper {
    display: flex;
    justify-content: flex-end;

    @include vp-1023 {
      z-index: $z-index-header-menu;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba($color-jaguar, 0.5);
      opacity: 0;
      pointer-events: none;
      visibility: hidden;
      transition: opacity $trans-default, visibility $trans-default;

      .is-open & {
        opacity: 1;
        pointer-events: all;
        visibility: visible;
        cursor: pointer;
      }
    }
  }

  &__content {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    column-gap: 64px;
    row-gap: 20px;
    min-width: 82.8%;

    @include vp-1023 {
      flex-direction: column;
      flex-wrap: nowrap;
      column-gap: initial;
      background: $color-black-100;
      justify-content: flex-start;
      align-items: flex-start;
      min-width: auto;
      width: 80%;
      padding: 48px;
      cursor: default;

      overflow-y: scroll;

      &::-webkit-scrollbar {
        width: 2px;
      }

      &::-webkit-scrollbar-thumb {
        background: $heliotrope;
      }
    }
  }

  &__list {
    max-width: 600px;

    @include vp-1023 {
      max-width: none;
      margin-bottom: 20px;
    }
  }

  &__btn {
    flex-shrink: 0;
    max-width: 128px;
    height: min-content;

    @include vp-1023 {
      margin-bottom: 20px;
    }
  }

  &__toggle {
    position: relative;
    color: $color-default-white;

    cursor: pointer;
    user-select: none;

    transition: color $trans-default, opacity $trans-default;
    transform: translateZ(0);

    &:disabled {
      opacity: 0.5;
      pointer-events: none;
    }

    @include hover-focus {
      opacity: 0.8;
    }

    &--burger {
      position: relative;
      z-index: $z-index-header-button;
      width: 32px;
      height: 32px;
      padding: 8px;
      background: none;
      border: 1px solid;
      border-image: $blue-to-right;
      border-image-slice: 1;
      display: none;

      @include vp-1023 {
        display: inline-block;
      }

      &::before,
      &::after,
      & span {
        content: "";
        position: absolute;
        right: 10px;
        height: 1px;
        color: $color-default-white;
        background-color: $color-default-white;
        transition: background-color $trans-default, transform $trans-default;
      }

      &::before {
        top: 11px;
        width: 11px;
      }

      & span {
        top: 15px;
        width: 8px;
      }

      &::after {
        bottom: 11px;
        width: 4px;
      }

      .is-open & {
        span {
          width: 0;
        }

        &::before,
        &::after {
          top: 50%;
          right: 9px;
          width: 11px;
        }

        &::before {
          transform: rotate(-45deg);
        }

        &::after {
          transform: rotate(45deg);
        }
      }
    }
  }
}
