.pricetag {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px 70px 5px 70px;
  max-width: 325px;
  border: 1px solid;
  background: $color-jaguar;
  border-image: $blue-to-right;
  border-image-slice: 1;

  word-break: break-word;

  @include vp-429 {
    padding: 20px 20px 5px 20px;
  }

  &__title {
    margin: 0;
    font-family: $ff-chakra-petch;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    color: $color-light-grey;
  }

  &__price {
    margin: 0 0 14px;
    font-family: $ff-chakra-petch;
    font-size: 60px;
    font-weight: 600;
    line-height: 66px;
    color: $color-default-white;
  }

  &__btn {
    margin-bottom: 12px;
  }

  &__additional-info {
    margin: 0 0 12px 0;
    font-family: $ff-chakra-petch;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    color: $color-spun-pearl;
  }
}
