.socials {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
    margin: 0;
    padding: 0;
    list-style: none;
    max-width: 244px;
  }

  &__item {
    display: flex;
    align-items: center;

    .icon-wrapper {
      width: 46px;
      height: 46px;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}
