.clients {
  font-family: $ff-chakra-petch;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 19px 0 3px;

    @include vp-1023 {
      margin: 0 19px 0 0;
    }
  }

  &__title {
    display: flex;
    align-items: center;
  }

  &__text {
    margin-bottom: 0;
  }

  &__number {
    position: relative;
    margin-bottom: 0;
    margin-right: 5px;
    color: $color-default-white;
    font-size: 16px;
    font-weight: 600;
    line-height: 28px;

    .svg-wrapper {
      position: absolute;
      left: -10px;
      bottom: 13px;
      width: 48px;
      height: 7px;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}
