.share {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .icon-wrapper {
    width: 22px;
    height: 22px;

    svg {
      width: 100%;
      height: 100;
    }
  }
}
