.vrnas-overview {
  &__wrapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column-reverse;
    }
  }

  &__title-group {
    margin-bottom: 6px;
  }

  &__images {
    @include vp-1023 {
      margin: 0 auto;
    }
  }

  &__content {
    margin: 38px 140px 0 0;
    max-width: 630px;

    @include vp-1023 {
      margin: 0 0 15px 0;
      max-width: none;
      width: 100%;
    }
  }
}
