@import "../keyframes.scss";

.partners-list {
  margin: 0;
  border-radius: 50%;
  list-style: none;

  &__item {
    position: absolute;
    display: inline-flex;
    transform: translateZ(0);

    .logo-wrapper {
      width: 102px;
      height: 102px;
      border-radius: 50%;

      @include vp-1023 {
        width: 70px;
        height: 70px;
      }

      @include vp-767 {
        width: 42px;
        height: 42px;
      }

      svg,
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
  }
}
