.title {
  display: inline-block;
  margin: 0;
  font-family: $ff-chakra-petch;
  font-size: 48px;
  font-weight: 600;
  line-height: 58px;
  letter-spacing: -0.24px;
  color: inherit;
  word-break: break-word;

  @include vp-1023 {
    font-size: 32px;
    line-height: normal;
  }

  &--h1 {
    font-size: 64px;
    line-height: normal;
    letter-spacing: -0.32px;

    @include vp-1023 {
      font-size: 48px;
      line-height: 58px;
    }
  }

  &--h3 {
    font-size: 20px;
    font-weight: 400;
    line-height: normal;
    text-transform: capitalize;
  }

  &--gradient {
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 2.24px;
    text-transform: uppercase;
    background-color: $color-default-white;
    background-image: $blue-to-right;
    background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
