.post {
  position: relative;
  display: flex;
  flex-direction: column;

  &__headline {
    display: flex;
    flex-direction: column;
  }

  &__badges {
    margin-bottom: 12px;
  }

  &__title {
    font-size: 40px;
    margin-bottom: 13px;

    @include vp-1023 {
      margin-bottom: 16px;
      font-size: 32px;
    }
  }

  &__info {
    flex-wrap: wrap;
    margin-bottom: 24px;
  }

  .info {
    display: inline-flex;
    flex-wrap: wrap;
    width: fit-content;
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 2.24px;
    text-transform: uppercase;

    &__author {
      margin: 0;
    }

    &__item {
      display: inline-block;
      position: relative;
      padding-right: 19px;
      background-color: $color-default-white;
      background-image: $blue-to-right;
      background-clip: text;
      -webkit-text-fill-color: transparent;

      &:not(:last-child)::after {
        content: "";
        position: absolute;
        top: 2px;
        right: 11px;
        bottom: 2px;
        box-sizing: border-box;
        border-right: 1px solid;
        border-image-slice: 1;
        border-width: 1px;
        border-image-source: $blue-to-right;
      }
    }
  }

  &__content {
    width: 100%;
  }

  &__subtitle {
    font-size: 16px;
    font-weight: 600;
    line-height: 28px;
    margin: 0 0 12px;
  }

  &__hashtags {
    margin-bottom: 16px;

    @include vp-767 {
      margin-bottom: 23px;
    }
  }
}
