.info-links {
  &__title {
    margin: 0 0 16px 0;
    font-family: $ff-chakra-petch;
    font-size: 16px;
    font-weight: 600;
    line-height: 28px;

    @include vp-1023 {
      margin-right: 108px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    font-family: $ff-chakra-petch;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
    color: $color-default-white;
  }

  &__link {
    display: flex;
    align-items: center;

    .icon-wrapper {
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      margin-right: 8px;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}
