.our-pricing {
  position: relative;
  padding-bottom: 47px;

  @include vp-1023 {
    padding-bottom: 85px;
  }

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    bottom: 36.5%;
    left: 66.5%;
    filter: blur(105px);
    opacity: 0.8;
    transform: translate3d(0, 0, 0);
    will-change: transform;

    @include vp-767 {
      left: 70%;
      bottom: 16%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 826px;
      height: 363px;
      background: $blue-to-bottom;
      clip-path: path("M474.598 39.3388C677.758 39.3388 826 -60.0871 826 57.1933C826 174.474 293.453 363 90.2932 363C-112.867 363 90.2933 174.474 90.2933 57.1934C90.2932 -60.0871 271.438 39.3388 474.598 39.3388Z");

      @include vp-767 {
        width: 603px;
        height: 265px;
        clip-path: path("M310.468 28.7184C458.779 28.7184 567 -43.8653 567 41.7527C567 127.371 178.228 265 29.9163 265C-118.395 265 29.9163 127.371 29.9163 41.7527C29.9163 -43.8652 162.156 28.7184 310.468 28.7184Z");
      }
    }
  }

  &__title-group {
    margin-bottom: 5px;

    @include vp-1023 {
      margin-bottom: 5px;
    }
  }

  &__content {
    position: relative;
  }

  &__text-wrapper {
    margin-bottom: 16px;
    max-width: 625px;

    @include vp-1023 {
      max-width: none;
      margin-bottom: 22px;
    }
  }

  &__detales {
    position: relative;
    display: flex;
    padding: 55px 0 10px 42px;
    background-color: rgba($color-jaguar, 0.82);
    border: 1px solid rgba($color-default-white, 0.1);

    @include vp-1279 {
      flex-direction: column;
      padding: 32px 22px 0 22px;
    }
  }

  &__list {
    .benefits-list {
      margin-right: 40px;
      padding-right: 118px;
      border-right: 1px solid rgba($color-default-white, 0.1);

      @include vp-1279 {
        margin: 0 0 37px 0;
        padding-right: 0;
        padding-bottom: 15px;
        border-right: none;
        border-bottom: 1px solid rgba($color-default-white, 0.1);
      }
    }
  }

  &__image {
    position: relative;
    bottom: -10px;
    flex-shrink: 0;
    margin-top: auto;
    overflow: hidden;
    width: 360px;
    height: 520px;

    @include vp-1279 {
      bottom: 0;
      right: -22px;
      margin-left: auto;
    }

    @include vp-1023 {
      width: 256px;
      height: 370px;
    }

    @include vp-374 {
      right: 0;
    }

    &::before {
      content: "";
      display: block;
      padding-bottom: calc(360 / 520 * 100%);
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__pricetag {
    position: absolute;
    right: 14.2%;
    bottom: -47px;

    @include vp-1279 {
      right: 15.3%;
      bottom: -87px;
    }
  }
}
