.team-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 9px;
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;

  @include vp-1023 {
    gap: 24px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-basis: 233px;
    max-width: 24.5%;

    @include vp-1023 {
      flex-grow: 0;
      flex-basis: auto;
      max-width: none;
      width: 293px;
    }
  }

  .card {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 9px;
    width: 100%;
    background: rgba($color-jaguar, 0.6);

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      box-sizing: border-box;
      border: 1px solid transparent;
      border-image-slice: 1;
      border-width: 1px;
      border-image-source: $gray-to-black;
      opacity: 1;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      box-sizing: border-box;
      border: 1px solid transparent;
      border-image-slice: 1;
      border-width: 1px;
      border-image-source: $blue-to-right;
      opacity: 0;
      transition: opacity $trans-default;
    }

    @include hover-focus {
      &::after {
        opacity: 1;
      }
    }

    &__image {
      position: relative;
      overflow: hidden;
      width: 100%;

      &::before {
        content: "";
        display: block;
        padding-bottom: calc(370 / 274 * 100%);
      }

      &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $dark-blue-gradient;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .card__info {
      position: absolute;
      overflow: hidden;
      bottom: 2%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: auto 6% 10px;
    }

    &__name {
      margin: 0;
      font-family: $ff-chakra-petch;
      font-size: 16px;
      font-weight: 600;
      line-height: 28px;
      color: $color-default-white;

      // stylelint-disable-next-line
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }

    &__position {
      margin: 0;
      font-family: $ff-chakra-petch;
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;
      color: $color-spun-pearl;

      // stylelint-disable-next-line
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
