.video {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 520px;
  border-radius: 12px;
  overflow: hidden;

  @include vp-767 {
    height: 254px;
  }

  &__overlay {
    position: absolute;
    z-index: $z-index-video-overlay;
    display: flex;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 52px 34px;
    opacity: 1;
    visibility: visible;
    pointer-events: none;
    transition: opacity $trans-default, visibility $trans-default;

    @include vp-1023 {
      padding: 22px 16px;
    }

    .play-button {
      position: absolute;
      display: inline-flex;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
      padding: 0;
      border: none;
      border-radius: 50%;
      background-color: $color-transparent;

      cursor: pointer;
      pointer-events: auto;
      user-select: none;
      transition: opacity $trans-default;

      .svg-wrapper {
        width: 100px;
        height: 100px;

        @include vp-1023 {
          width: 40px;
          height: 40px;
        }

        svg {
          width: 100%;
          height: 100%;
        }
      }

      @include hover-focus {
        opacity: 0.8;
      }

      &:active {
        opacity: 0.8;
      }
    }
  }

  &.is-active {
    .video__overlay {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__tags {
    margin: 0 0 12px 0;
  }

  &__title {
    margin: 0 0 12px 0;
    font-family: $ff-chakra-petch;
    font-size: 36px;
    font-weight: 500;
    line-height: 44px;
    letter-spacing: -0.72px;

    @include vp-1023 {
      font-size: 20px;
      font-weight: 500;
      line-height: normal;
    }
  }

  &__content {
    video {
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}
