.accordion {
  &__element:last-child {
    margin-bottom: 0;
  }

  &__element {
    margin-bottom: 47px;
  }

  &__button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    margin: 0;
    padding: 0 0 12px 0;
    width: 100%;

    font-family: $ff-chakra-petch;
    font-size: 20px;
    font-weight: 400;
    line-height: normal;

    color: $color-default-white;
    background-color: $color-transparent;
    border: none;
    border-bottom: 1px solid rgba($color-default-white, 0.35);
    transition: opacity $trans-default;

    cursor: pointer;

    @include hover-focus {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.8;
    }

    .svg-wrapper {
      display: flex;
      flex-shrink: 0;
      width: 12px;
      height: 12px;
      margin-left: 10px;
      justify-content: center;
      align-items: center;

      @include vp-1023 {
        margin-right: 10px;
      }

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__text {
    padding-top: 12px;
    font-family: $ff-chakra-petch;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    color: $color-light-grey;

    p {
      margin: 0 0 24px 0;

      &:only-child,
      &:last-child {
        margin: 0;
      }
    }
  }

  .is-active {
    .svg-wrapper {
      transform: rotate(180deg);
    }

    .accordion__button {
      font-weight: 500;
    }
  }
}
