.grid-item {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "icon"
    "text";
  row-gap: 59px;
  padding: 38px 32px 25px 32px;
  height: 291px;
  border: 1px solid $color-transparent;
  background-color: rgba($color-jaguar, 0.6);
  pointer-events: all;
  overflow: hidden;
  width: 100%;

  &__icon-wrapper {
    grid-area: icon;
    width: 48px;
    height: 48px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    grid-area: text;
  }

  &__title {
    margin-bottom: 12px;

    // stylelint-disable-next-line
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }

  &__description {
    font-family: $ff-chakra-petch;
    font-size: 14px;
    font-weight: 400;
    line-height: 28px;
    color: $color-spun-pearl;

    // stylelint-disable-next-line
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;

    p {
      margin: 0;
      padding: 0;
    }
  }

  .details {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    left: 0;
    bottom: 0;
    width: 100%;
    min-height: 50%;
    max-height: 100%;
    background: $dark-background-gradient;
    transition: opacity $trans-default;
    opacity: 0;
    transform: translate3d(0, 0, 0);

    &:focus-within {
      opacity: 1;
    }

    &__btn {
      display: inline-flex;
      flex-direction: column;
      justify-content: flex-start;
      margin: 38px 32px 28px 32px;
      overflow: hidden;

      @include vp-1023 {
        margin: 38px 32px 21px 32px;
      }
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    z-index: -1;
    border: 1px solid;
    border-image-slice: 1;
    border-width: 1px;
    border-image-source: $gray-to-black;
    // transition: opacity $trans-default;
    opacity: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    z-index: 1;
    border: 1px solid;
    border-image-slice: 1;
    border-width: 1px;
    border-image-source: $blue-to-right;
    transition: opacity $trans-default;
    pointer-events: none;
    opacity: 0;
  }

  @include hover-focus {
    &::before {
      opacity: 0;
    }

    &::after {
      opacity: 1;
    }

    .details {
      opacity: 1;
    }
  }

  &:focus {
    &::before {
      opacity: 0;
    }

    &::after {
      opacity: 1;
    }

    .details {
      opacity: 1;
    }
  }
}
