.post-card {
  display: flex;

  &__image {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .image-wrapper {
    width: 102px;
    height: 65px;
    margin: 0 15px 0 0;

    @include vp-374 {
      width: 71px;
      height: 45px;
      margin: 0 10px 5px 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    flex-direction: column;
  }

  &__tag {
    font-size: 10px;
    padding: 2px 7px;
    margin-bottom: 7px;
  }

  &__title {
    font-family: $ff-chakra-petch;
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    margin: 6px 0 3px 0;

    // stylelint-disable-next-line
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
