.benefits-list {
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 31px;

    .svg-wrapper {
      width: 42px;
      height: 42px;
      flex-shrink: 0;
      margin-right: 16px;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__title {
    font-size: 18px;
    margin: 0 0 12px 0;
  }

  &__description {
    font-family: $ff-chakra-petch;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    color: $color-spun-pearl;

    p {
      margin: 0;
    }
  }
}
