.intro-about-us {
  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 80px 0 77px 0;

    @include vp-1023 {
      padding-top: 45px;
      padding-bottom: 63px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-image: $blue-to-right;
    }
  }


  &__title {
    margin-bottom: 28px;

    @include vp-1023 {
      margin-bottom: 26px;
    }
  }

  &__background {
    position: absolute;
    z-index: -1;
    filter: blur(55px);
    transform: translate3d(0, 0, 0);
    will-change: transform;

    &::before {
      content: "";
      position: absolute;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 449px;
      height: 147px;
      background: $blue-to-bottom;
      clip-path: path("M257.984 15.9306C368.418 15.9306 449 -24.3328 449 23.1609C449 70.6547 159.516 147 49.0819 147C-61.3524 147 49.0819 70.6547 49.0819 23.1609C49.0819 -24.3328 147.549 15.9306 257.984 15.9306Z");

      @include vp-767 {
        width: 299px;
        height: 98px;
        clip-path: path("M171.798 10.6204C245.338 10.6204 299 -16.2219 299 15.4406C299 47.1031 106.226 98 32.6848 98C-40.856 98 32.6848 47.1031 32.6848 15.4406C32.6848 -16.2218 98.2566 10.6204 171.798 10.6204Z");
      }
    }
  }
}
