.nav-list {
  display: flex;
  flex-wrap: wrap;
  column-gap: 64px;
  row-gap: 20px;
  margin: 0;
  padding: 0;

  list-style: none;

  @include vp-1023 {
    flex-direction: column;
    column-gap: 0;
    justify-content: start;
    text-align: left;
    margin-bottom: 20px;
  }

  &__item {
    @include vp-1023 {
      margin-top: 20px;
    }
  }

  &__link {
    color: $color-default-white;

    &.is-active {
      font-weight: 600;
    }
  }
}
