.footer {
  padding: 119px 0 40px 0;
  margin: 0;
  background-color: $color-transparent;

  @include vp-1023 {
    padding: 0 0 21px 0;
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-image: $blue-to-right;
    }

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__social-links {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 180px 68px 0;

    @include vp-1023 {
      flex-direction: row;
      justify-content: flex-start;
      margin: 0 0 49px 0;
    }
  }

  &__logo {
    margin: 0 52px 16px 0;
    flex-shrink: 0;

    @include vp-1023 {
      margin: 8px 52px 16px 0;
    }
  }

  &__menu {
    display: flex;
    flex-wrap: wrap;
    margin: -30px 0 33px -30px;

    flex-grow: 1;

    @include vp-1023 {
      justify-content: flex-start;
      margin: -20px 0 48px -20px;
    }
  }

  &__links-list {
    max-width: 320px;
    margin: 30px 0 0 30px;

    @include vp-1023 {
      margin: 20px 0 0 20px;

      &:not(:last-child) {
        margin-bottom: 44px;
      }
    }

    .info-links__title {
      margin-right: 126px;

      @include vp-1023 {
        margin-right: 88px;
      }
    }
  }

  &__authorship {
    text-align: center;
  }

  &__copyright {
    margin: 0;
    margin-top: 32px;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
  }
}
