.text-content {
  font-family: $ff-chakra-petch;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  color: $color-light-grey;
  width: 100%;

  figure {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 34px;
    margin: 0 0 30px 0;
    width: 100%;

    picture:only-child,
    img:only-child {
      grid-column: span 2;
    }

    @include vp-1023 {
      margin-bottom: 24px;
      gap: 17px;
    }

    img {
      width: 100%;
      max-height: 600px;
      object-fit: cover;
    }
  }

  p {
    margin: 0 0 24px 0;
  }

  ul {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      display: flex;
      align-items: baseline;
      margin-bottom: 14px;
      font-family: $ff-chakra-petch;
      font-size: 16px;
      font-weight: 400;
      line-height: 28px;

      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 16px;
        height: 16px;
        margin-right: 12px;
        flex-shrink: 0;

        svg,
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}
