.why-choose-us {
  &__wrapper {
    display: flex;
    flex-direction: row-reverse;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  .title-group {
    margin-bottom: 20px;
  }

  &__content {
    margin-top: 94px;
    margin-right: 135px;

    @include vp-1023 {
      margin: 48px 0 0 0;
      width: 100%;
    }
  }

  &__image {
    @include vp-1023 {
      margin: 0 auto;
    }
  }

  &--left {
    .why-choose-us__wrapper {
      flex-direction: row;

      @include vp-1023 {
        flex-direction: column;
      }
    }

    .why-choose-us__image {
      margin-right: 93px;

      @include vp-1023 {
        margin: 0 auto;
      }
    }

    .why-choose-us__content {
      margin-top: 66px;
      margin-right: 0;
      max-width: 548px;

      @include vp-1023 {
        margin: 48px 0 0 0;
        max-width: none;
      }
    }
  }
}
