.custom-input {
  position: relative;
  display: flex;

  label {
    display: flex;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }

    input,
    textarea {
      flex-grow: 1;
      width: 365px;
      height: 55px;
      padding: 13px 66px 13px 46px;
      color: $color-default-white;
      border: 1px solid $color-transparent;
      background: rgba($color-default-white, 0.07);
      border-radius: 2px;
      outline: none;
      transition: border-color $trans-default;
      caret-color: $color-default-white;

      @include vp-429 {
        width: 280px;
      }

      &::placeholder {
        font-family: $ff-chakra-petch;
        font-size: 16px;
        font-weight: 400;
        line-height: 28px;
        color: $color-default-white;

        @include vp-429 {
          font-size: 14px;
        }
      }

      &:-webkit-autofill {
        -webkit-text-fill-color: $color-default-white;
        // stylelint-disable-next-line
        -webkit-box-shadow: 0 0 0 30px $color-transparent inset;
        transition: background-color $trans-input-autofill;

        &::first-line {
          font-size: 16px;
        }
      }

      @include hover-focus {
        border-color: rgba($color-spun-pearl, 0.8);
      }
    }
  }

  &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: 35px;

    color: $coral-red;

    opacity: 0;

    transition: opacity $trans-default;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  }

  &.is-invalid {
    input,
    textarea {
      border: 1px solid $coral-red;

      &:focus,
      &:hover {
        border: 1px solid rgba($coral-red, 0.5);
      }
    }
  }

  &.is-valid {
    input {
      border: 1px solid $color-jade;

      &:focus,
      &:hover {
        border: 1px solid rgba($color-jade, 0.5);
      }
    }
  }

  .input-message {
    position: absolute;
    left: 0;
    bottom: -22px;

    &.is-invalid {
      color: $coral-red;
    }

    &.is-valid {
      color: $color-jade;
    }
  }
}
