.about-us {
  &__wrapper {
    display: flex;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__images {
    margin-right: 106px;

    @include vp-1023 {
      margin: 0 auto;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    margin-top: 78px;
    max-width: 540px;

    @include vp-1023 {
      margin-top: 48px;
      max-width: none;
    }
  }

  &__content {
    margin-bottom: 18px;
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
