.our-service {
  position: relative;

  &__text-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    text-align: start;
    margin-bottom: 22px;

    @include vp-1023 {
      flex-direction: column;
      margin-bottom: 0;
    }
  }

  &__title-group {
    margin-right: 50px;
    max-width: 400px;

    @include vp-1023 {
      margin: 0;
      max-width: none;
    }
  }

  &__description {
    margin: 2px 41px 0 0;
    max-width: 625px;

    @include vp-1023 {
      margin: 0;
      max-width: none;
    }
  }

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    top: 12.5%;
    right: 14%;
    filter: blur(105px);
    opacity: 0.8;
    transform: translate3d(0, 0, 0);
    will-change: transform;

    @include vp-1023 {
      visibility: hidden;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 403px;
      height: 199px;
      background: $blue-to-bottom;
      clip-path: path("M260.856 21.5659C372.521 21.5659 454 -32.9403 454 31.3539C454 95.6482 161.293 199 49.6285 199C-62.0356 199 49.6285 95.6482 49.6285 31.3539C49.6285 -32.9403 149.192 21.5659 260.856 21.5659Z");
    }
  }
}
