.our-team {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 28px;

    @include vp-1023 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      margin-bottom: 19px;
    }
  }

  &__btn {
    margin-bottom: 16px;

    @include vp-1023 {
      margin-bottom: 0;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .team-list {
      @include vp-1023 {
        margin-bottom: 32px;
      }
    }
  }
}
