@font-face {
  font-family: "Chakra Petch";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/ChakraPetch-Light.woff") format("woff2"), url("../fonts/ChakraPetch-Light.woff2") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ChakraPetch-Regular.woff2") format("woff2"), url("../fonts/ChakraPetch-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/ChakraPetch-Medium.woff2") format("woff2"), url("../fonts/ChakraPetch-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/ChakraPetch-SemiBold.woff2") format("woff2"), url("../fonts/ChakraPetch-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Chakra Petch";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ChakraPetch-Bold.woff2") format("woff2"), url("../fonts/ChakraPetch-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Chakra Petch", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #18181b;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  padding-top: 114px;
}
@media (max-width: 767px) {
  .wrapper main {
    padding-top: 106px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  z-index: 30;
  font-family: "Chakra Petch", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px;
  padding: 29px 0;
  transition: background-color 0.3s ease;
}
@media (max-width: 1023px) {
  .header {
    padding: 39px 0;
  }
}
.header--sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.header--base {
  background-color: rgba(255, 255, 255, 0);
}
.header--blur:not(.is-open) {
  background-color: rgba(37, 37, 50, 0.8);
  backdrop-filter: blur(5px);
  transform: translate3d(0, 0, 0);
  will-change: transform;
}
.header__wrapper {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
}
.header__logo {
  display: flex;
  color: #ffffff;
  margin-right: 44px;
}

.main-nav {
  flex-grow: 1;
}
@media (max-width: 1023px) {
  .main-nav {
    flex-grow: 0;
  }
}
.main-nav__wrapper {
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 1023px) {
  .main-nav__wrapper {
    z-index: 10;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(37, 37, 50, 0.5);
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: opacity 0.3s ease, visibility 0.3s ease;
  }
  .is-open .main-nav__wrapper {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
    cursor: pointer;
  }
}
.main-nav__content {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  column-gap: 64px;
  row-gap: 20px;
  min-width: 82.8%;
}
@media (max-width: 1023px) {
  .main-nav__content {
    flex-direction: column;
    flex-wrap: nowrap;
    column-gap: initial;
    background: #18181b;
    justify-content: flex-start;
    align-items: flex-start;
    min-width: auto;
    width: 80%;
    padding: 48px;
    cursor: default;
    overflow-y: scroll;
  }
  .main-nav__content::-webkit-scrollbar {
    width: 2px;
  }
  .main-nav__content::-webkit-scrollbar-thumb {
    background: #ca6aeb;
  }
}
.main-nav__list {
  max-width: 600px;
}
@media (max-width: 1023px) {
  .main-nav__list {
    max-width: none;
    margin-bottom: 20px;
  }
}
.main-nav__btn {
  flex-shrink: 0;
  max-width: 128px;
  height: min-content;
}
@media (max-width: 1023px) {
  .main-nav__btn {
    margin-bottom: 20px;
  }
}
.main-nav__toggle {
  position: relative;
  color: #ffffff;
  cursor: pointer;
  user-select: none;
  transition: color 0.3s ease, opacity 0.3s ease;
  transform: translateZ(0);
}
.main-nav__toggle:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .main-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__toggle--burger {
  position: relative;
  z-index: 11;
  width: 32px;
  height: 32px;
  padding: 8px;
  background: none;
  border: 1px solid;
  border-image: linear-gradient(90deg, #0cbaf1 0, #e95ce9 100%);
  border-image-slice: 1;
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__toggle--burger {
    display: inline-block;
  }
}
.main-nav__toggle--burger::before, .main-nav__toggle--burger::after, .main-nav__toggle--burger span {
  content: "";
  position: absolute;
  right: 10px;
  height: 1px;
  color: #ffffff;
  background-color: #ffffff;
  transition: background-color 0.3s ease, transform 0.3s ease;
}
.main-nav__toggle--burger::before {
  top: 11px;
  width: 11px;
}
.main-nav__toggle--burger span {
  top: 15px;
  width: 8px;
}
.main-nav__toggle--burger::after {
  bottom: 11px;
  width: 4px;
}
.is-open .main-nav__toggle--burger span {
  width: 0;
}
.is-open .main-nav__toggle--burger::before, .is-open .main-nav__toggle--burger::after {
  top: 50%;
  right: 9px;
  width: 11px;
}
.is-open .main-nav__toggle--burger::before {
  transform: rotate(-45deg);
}
.is-open .main-nav__toggle--burger::after {
  transform: rotate(45deg);
}

.footer {
  padding: 119px 0 40px 0;
  margin: 0;
  background-color: rgba(255, 255, 255, 0);
}
@media (max-width: 1023px) {
  .footer {
    padding: 0 0 21px 0;
  }
}
.footer .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.footer__wrapper::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-image: linear-gradient(90deg, #0cbaf1 0, #e95ce9 100%);
}
@media (max-width: 1023px) {
  .footer__wrapper {
    flex-direction: column;
  }
}
.footer__social-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 180px 68px 0;
}
@media (max-width: 1023px) {
  .footer__social-links {
    flex-direction: row;
    justify-content: flex-start;
    margin: 0 0 49px 0;
  }
}
.footer__logo {
  margin: 0 52px 16px 0;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin: 8px 52px 16px 0;
  }
}
.footer__menu {
  display: flex;
  flex-wrap: wrap;
  margin: -30px 0 33px -30px;
  flex-grow: 1;
}
@media (max-width: 1023px) {
  .footer__menu {
    justify-content: flex-start;
    margin: -20px 0 48px -20px;
  }
}
.footer__links-list {
  max-width: 320px;
  margin: 30px 0 0 30px;
}
@media (max-width: 1023px) {
  .footer__links-list {
    margin: 20px 0 0 20px;
  }
  .footer__links-list:not(:last-child) {
    margin-bottom: 44px;
  }
}
.footer__links-list .info-links__title {
  margin-right: 126px;
}
@media (max-width: 1023px) {
  .footer__links-list .info-links__title {
    margin-right: 88px;
  }
}
.footer__authorship {
  text-align: center;
}
.footer__copyright {
  margin: 0;
  margin-top: 32px;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
}

.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 18px 38px;
  letter-spacing: 1.5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  background: linear-gradient(90deg, #0cbaf1 0, #e95ce9 100%);
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--transparent {
  padding: 12px 24px;
  letter-spacing: normal;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  text-transform: none;
  border: 1px solid;
  background: none;
  border-image: linear-gradient(90deg, #0cbaf1 0, #e95ce9 100%);
  border-image-slice: 1;
}
.btn--transparent-big {
  padding: 18px 38px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
}
@media (hover: hover) {
  .btn--transparent-big:hover:not(.focus-visible) {
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.06);
  }
}
.btn--transparent-big.focus-visible:focus {
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.06);
}
.btn--round {
  position: relative;
  width: 46px;
  height: 46px;
  padding: 0;
  border-radius: 50%;
  border-width: 0;
  cursor: pointer;
}
.btn--round svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.btn--submit {
  padding: 16px;
  background: rgba(255, 255, 255, 0.21);
}
.btn--submit .svg-wrapper {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}
.btn--submit .svg-wrapper svg {
  width: 100%;
  height: 100%;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn:active {
  opacity: 0.8;
}

.custom-input {
  position: relative;
  display: flex;
}
.custom-input label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea {
  flex-grow: 1;
  width: 365px;
  height: 55px;
  padding: 13px 66px 13px 46px;
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0);
  background: rgba(255, 255, 255, 0.07);
  border-radius: 2px;
  outline: none;
  transition: border-color 0.3s ease;
  caret-color: #ffffff;
}
@media (max-width: 429px) {
  .custom-input label input,
.custom-input label textarea {
    width: 280px;
  }
}
.custom-input label input::placeholder,
.custom-input label textarea::placeholder {
  font-family: "Chakra Petch", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  color: #ffffff;
}
@media (max-width: 429px) {
  .custom-input label input::placeholder,
.custom-input label textarea::placeholder {
    font-size: 14px;
  }
}
.custom-input label input:-webkit-autofill,
.custom-input label textarea:-webkit-autofill {
  -webkit-text-fill-color: #ffffff;
  -webkit-box-shadow: 0 0 0 30px rgba(255, 255, 255, 0) inset;
  transition: background-color 5000s ease-in-out 0s;
}
.custom-input label input:-webkit-autofill::first-line,
.custom-input label textarea:-webkit-autofill::first-line {
  font-size: 16px;
}
@media (hover: hover) {
  .custom-input label input:hover:not(.focus-visible),
.custom-input label textarea:hover:not(.focus-visible) {
    border-color: rgba(176, 176, 178, 0.8);
  }
}
.custom-input label input.focus-visible:focus,
.custom-input label textarea.focus-visible:focus {
  border-color: rgba(176, 176, 178, 0.8);
}
@media screen and (max-width: 767px) {
  .custom-input__label {
    margin: 0 0 18px 0;
  }
}
.custom-input__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff4747;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea {
