.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;

  &__item:nth-child(3n+1) {
    margin-top: -30px;
  }

  &__item:nth-child(3n+3) {
    margin-top: 30px;
  }

  &__item:nth-child(1) {
    margin-top: 0;
  }

  &__item:nth-child(2) {
    margin-top: 30px;
  }

  &__item:nth-child(3) {
    margin-top: 60px;
  }

  @include vp-1023 {
    grid-template-columns: 1fr;
    gap: 32px;

    & &__item {
      margin: 0;
    }
  }
}
