.breadcrumbs-block {
  position: relative;

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 50px 0 52px 0;

    @include vp-1023 {
      margin: 0 0 43px 0;
    }
  }

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translate(-50%, -50%) translate3d(0, 0, 0);
    filter: blur(55px);
    visibility: hidden;

    will-change: transform;

    @include vp-767 {
      visibility: visible;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 299px;
      height: 98px;
      background: $blue-to-bottom;
      clip-path: path("M171.798 10.6204C245.338 10.6204 299 -16.2219 299 15.4406C299 47.1031 106.226 98 32.6848 98C-40.856 98 32.6848 47.1031 32.6848 15.4406C32.6848 -16.2218 98.2566 10.6204 171.798 10.6204Z");
    }
  }
}
