.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 18px 38px;
  letter-spacing: 1.5px;

  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
  color: $color-default-white;

  border: none;
  background: $blue-to-right;

  cursor: pointer;
  user-select: none;

  transition: opacity $trans-default;

  &--transparent {
    padding: 12px 24px;
    letter-spacing: normal;
    font-size: 16px;
    font-weight: 300;
    line-height: 28px;
    text-transform: none;
    border: 1px solid;
    background: none;
    border-image: $blue-to-right;
    border-image-slice: 1;
  }

  &--transparent-big {
    padding: 18px 38px;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    transition: background-color $trans-default;

    @include hover-focus {
      opacity: 1;
      background-color: rgba($color-default-white, 0.06);
    }
  }

  &--round {
    position: relative;
    width: 46px;
    height: 46px;
    padding: 0;
    border-radius: 50%;
    border-width: 0;
    cursor: pointer;

    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &--submit {
    padding: 16px;
    background: rgba($color-default-white, 0.21);

    .svg-wrapper {
      width: 16px;
      height: 16px;
      flex-shrink: 0;

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.8;
  }
}
