.intro-image-group {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 526px;

  @include vp-1023 {
    width: 100%;
    min-height: 422px;
  }

  @include vp-767 {
    min-height: 410px;
  }

  .positioned-image {
    position: relative;
    margin-bottom: 5px;
  }

  &__image {
    position: relative;
    margin-top: auto;
    overflow: hidden;

    @include vp-1023 {
      position: absolute;
      top: 0;
      right: 0;
      width: 354px;
      height: 422px;
    }

    @include vp-767 {
      bottom: 0;
      right: -132px;
    }

    &::before {
      content: "";
      display: block;
      padding-bottom: calc(626 / 526 * 100%);
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    bottom: 255px;
    left: 50%;
    filter: blur(105px);
    opacity: 0.8;

    transform: translate(-50%, -50%) translate3d(0, 0, 0);
    will-change: transform;

    @include vp-1023 {
      left: 80%;
    }

    @include vp-767 {
      bottom: 166px;
      left: 72.5%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 567px;
      height: 340px;
      background: $blue-to-bottom;
      clip-path: path("M325.783 36.8463C465.24 36.8463 567 -56.2799 567 53.5695C567 163.419 201.438 340 61.981 340C-77.4762 340 61.981 163.419 61.981 53.5695C61.981 -56.2799 186.326 36.8463 325.783 36.8463Z");

      @include vp-767 {
        width: 251px;
        height: 229px;
        clip-path: path("M219.487 24.817C313.442 24.817 382 -37.9062 382 36.0806C382 110.067 135.713 229 41.7579 229C-52.1974 229 41.7579 110.067 41.7579 36.0807C41.7579 -37.9062 125.532 24.817 219.487 24.817Z");
      }
    }
  }
}
