.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100%;

  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;

  list-style: none;
  color: $color-default-white;

  @include vp-767 {
    flex-wrap: nowrap;
  }

  &__item {
    display: flex;
    align-items: center;

    .svg-wrapper {
      width: 18px;
      height: 18px;
      flex-shrink: 0;
      margin: 4px;
      transform: rotate(-90deg);
    }

    &:last-child {
      color: $color-light-grey;
      pointer-events: none;
      font-weight: 400;
      line-height: 28px;
    }
  }

  &__link {
    @include vp-767 {
      // stylelint-disable-next-line
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
