.post-list {
  display: inline-flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 24px 0;

    &:first-child {
      padding-top: 0;
    }

    &:not(:last-child) {
      border-bottom: 1px solid rgba($color-default-white, 0.15);
    }
  }
}
