.get-started {
  position: relative;

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    top: 26%;
    left: 15.5%;
    filter: blur(105px);
    opacity: 0.8;

    transform: translate3d(0, 0, 0);
    will-change: transform;

    @include vp-1023 {
      visibility: hidden;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      height: 199px;
      width: 454px;
      background: $blue-to-bottom;
      clip-path: path("M212.856 21.5659C324.521 21.5659 406 -32.9403 406 31.3539C406 95.6482 113.293 199 1.62849 199C-110.036 199 1.6285 95.6482 1.62849 31.3539C1.62849 -32.9403 101.192 21.5659 212.856 21.5659Z");
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__title-group {
    text-align: center;
    margin-bottom: 36px;
    max-width: 810px;

    @include vp-1023 {
      margin-bottom: 20px;
      max-width: none;
    }
  }

  &__video {
    margin-bottom: 48px;

    @include vp-1023 {
      margin-bottom: 32px;
    }
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
