.block-container {
  &__wrapper {
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__sidebar {
    display: flex;
    flex-direction: column;
    margin: 0 0 150px 77px;
    width: 100%;

    @include vp-1023 {
      margin: 0 0 100px 0;
    }

    .sidebar {
      width: 333px;

      @include vp-1023 {
        width: 100%;
      }
    }
  }
}
