.intro {
  padding-top: 12px;

  @include vp-1023 {
    padding-top: 5px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    max-width: 570px;

    @include vp-1023 {
      margin-right: 0;
    }

    .title {
      margin-top: 75px;
      margin-bottom: 12px;

      @include vp-1023 {
        margin-top: 43px;
      }
    }

    .btn {
      width: fit-content;
      margin-bottom: 42px;

      @include vp-1023 {
        margin-bottom: 32px;
      }
    }
  }

  &__text {
    max-width: 446px;
    margin: 0 0 8px 0;

    @include vp-767 {
      max-width: none;
    }
  }

  &__clients-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 39px;
    max-width: 550px;

    @include vp-1023 {
      margin-bottom: 0;
    }
  }

  &__clients-list {
    margin-right: 10px;
    margin-bottom: 5px;
    max-width: 300px;
  }

  &__image {
    overflow: hidden;
    width: 196px;
    height: 128px;
    margin-bottom: 5px;
    clip-path: inset(1px 1px);

    @include vp-1023 {
      margin-bottom: 0;
    }

    @include vp-429 {
      width: 125px;
      height: 75px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
