.layered-card {
  position: relative;
  display: inline-flex;
  margin-bottom: 4%;

  @include vp-767 {
    margin-bottom: 4%;
  }

  &__background-light {
    z-index: $z-index-background;
    position: absolute;
    bottom: 45%;
    left: 48.5%;
    filter: blur(105px);
    opacity: 0.5;

    transform: translate(-50%, -50%) translate3d(0, 0, 0);
    will-change: transform;

    @include vp-767 {
      left: 50%;
      filter: blur(78px);
    }

    &::before {
      content: "";
      position: absolute;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 609px;
      height: 365px;
      background: $blue-to-bottom;
      clip-path: path("M349.374 39.5217C498.93 39.5217 608.059 -60.3482 608.059 57.4559C608.059 175.26 216.025 364.628 66.4692 364.628C-83.0865 364.628 66.4692 175.26 66.4692 57.4559C66.4692 -60.3481 199.819 39.5217 349.374 39.5217Z");

      @include vp-767 {
        width: 430px;
        height: 275px;
        clip-path: path("M238.362 30.2997C350.83 30.2997 432.896 -44.8039 432.896 43.7865C432.896 132.377 138.081 274.784 25.6128 274.784C-86.8553 274.784 25.6128 132.377 25.6128 43.7865C25.6128 -44.8038 125.893 30.2997 238.362 30.2997Z");
      }
    }
  }

  &__blur-rectangle {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 399px;
    height: 497px;
    background: $color-jaguar;
    box-shadow: 0 4px 4px 0 rgba($color-default-black, 0.25);
    opacity: 0.3;

    @include vp-767 {
      left: -2px;
      width: 300px;
      height: 373px;
    }

    @include vp-429 {
      width: 195px;
      height: 242px;
    }
  }

  &__main-image {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 5%;
    bottom: 0;
    width: 517px;
    overflow: hidden;

    @include vp-767 {
      left: 3.5%;
      max-width: 390px;
    }

    @include vp-429 {
      width: 256px;
    }

    &::before {
      content: "";
      display: block;
      padding-bottom: calc(622 / 517 * 100%);
    }

    img {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      object-fit: contain;
    }
  }

  &__small-image {
    position: absolute;
    bottom: -3.5%;
    right: -2%;
    width: 196px;
    height: 128px;
    clip-path: inset(1px 1px);

    @include vp-1023 {
      bottom: -6%;
      right: 14%;
    }

    @include vp-767 {
      bottom: -4%;
      right: -0.5%;
      width: 147px;
      height: 96px;
    }

    @include vp-429 {
      width: 125px;
      height: 75px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &--left {
    margin-bottom: 8%;

    @include vp-1023 {
      margin-bottom: 6%;
    }

    @include vp-767 {
      margin-bottom: 9%;
    }

    .layered-card__main-image {
      left: -12%;
      max-width: 398px;

      @include vp-1023 {
        left: 7%;
      }

      @include vp-767 {
        max-width: 314px;
      }

      &::before {
        content: "";
        display: block;
        padding-bottom: calc(574 / 398 * 100%);
      }
    }

    .layered-card__blur-rectangle {
      left: -8%;

      @include vp-1023 {
        left: 11%;
      }
    }

    .layered-card__small-image {
      bottom: -6%;
      left: -32%;

      @include vp-1023 {
        left: -11%;
      }
    }
  }
}
