.partners {
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  &__title-group {
    margin-bottom: 46px;

    @include vp-1023 {
      margin-bottom: 13px;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &__details {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 1055px;

    @include vp-1279 {
      justify-content: flex-end;
      width: 976px;
    }

    @include vp-1023 {
      width: 100%;
    }

    .semicircle {
      z-index: -1;
      position: absolute;
      overflow: hidden;
      width: 100%;
      top: 0;

      @include vp-767 {
        min-width: 430px;
      }

      @include vp-429 {
        min-width: auto;
        width: 100%;
      }

      &::before {
        content: "";
        display: block;
        padding-bottom: calc(541 / 1054 * 100%);
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        width: 100%;
        height: 81px;
        background: $black-to-bottom;
      }
    }

    .image-card {
      position: relative;
      width: 67%;
      padding-top: 17.5%;

      @include vp-1023 {
        width: 57%;
      }

      @include vp-767 {
        padding-top: 18.5%;
        width: 72.5%;
      }

      &__background-light {
        position: absolute;
        bottom: 27%;
        left: 55%;
        filter: blur(105px);
        opacity: 0.8;

        transform: translate3d(0, 0, 0);
        will-change: transform;

        @include vp-767 {
          filter: blur(42px);
        }

        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 618px;
          height: 272px;
          background: $blue-to-bottom;
          clip-path: path("M355.087 29.477C507.087 29.477 618 -45.024 618 42.8556C618 130.735 219.557 272 67.556 272C-84.4449 272 67.556 130.735 67.556 42.8556C67.556 -45.0239 203.086 29.477 355.087 29.477Z");

          @include vp-767 {
            width: 253px;
            height: 111px;
            clip-path: path("M145.273 12.0993C207.23 12.0992 252.44 -18.2684 252.44 17.5526C252.44 53.3735 90.0287 110.955 28.071 110.955C-33.8868 110.955 28.071 53.3735 28.071 17.5526C28.071 -18.2684 83.3148 12.0993 145.273 12.0993Z");
          }
        }
      }

      &__image {
        position: relative;
        overflow: hidden;
        margin: 0 auto;
        width: 100%;

        &::before {
          content: "";
          display: block;
          padding-bottom: calc(557 / 708 * 100%);
        }

        &::after {
          content: "";
          position: absolute;
          z-index: -1;
          bottom: -1px;
          left: 50%;
          transform: translate(-50%, 0);
          padding-bottom: 64%;
          width: 64%;
          border: 1px solid rgba($color-default-white, 0.17);
          border-radius: 50%;
          background: rgba($color-jaguar, 0.8);
        }

        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  &__list {
    position: absolute;
    bottom: -32.5%;
    margin: 0;
    padding-bottom: 86.5%;
    width: 86.5%;

    @include vp-1023 {
      bottom: -49%;
    }

    @include vp-767 {
      bottom: -32%;
      width: 92%;
      padding-bottom: 92%;
    }
  }
}
