.badge {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2px 10px;
  border-radius: 16px;

  font-family: $ff-chakra-petch;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  background-color: rgba($color-default-white, 0.3);

  &--mini {
    font-size: 10px;
    line-height: 14px;
    padding: 1px 7px;
  }
}
