.subscribe-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: opacity $trans-default;

  padding: 38px 74px 31px;
  background: $blue-to-right;
  border-radius: 4px;

  @include vp-1023 {
    padding: 31px 13px 22px 13px;
  }

  @include vp-767 {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  &__title {
    margin: 0 25px 0 0;
    max-width: 521px;
    font-family: $ff-chakra-petch;
    font-size: 40px;
    font-weight: 600;
    line-height: 58px;
    letter-spacing: -0.2px;

    @include vp-1023 {
      font-size: 28px;
      font-weight: 600;
      line-height: normal;
      letter-spacing: -0.14px;
    }

    @include vp-767 {
      margin: 0 0 8px 0;
    }
  }

  &__form {
    max-width: 365px;

    form {
      position: relative;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      .icon {
        position: absolute;
        top: 16px;
        left: 14px;
        color: $color-default-white;
      }

      fieldset {
        display: flex;
        flex-direction: column;
        gap: 22px;
        margin: 0 0 8px 0;
        padding: 0;
        border: none;
      }
    }
  }

  &__btn {
    position: absolute;
    top: 4px;
    right: 4px;
  }

  .subscribe-form__message {
    display: none;
    font-family: $ff-chakra-petch;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
  }

  &.is-success {
    .subscribe-form__message {
      display: inline-flex;
      color: $color-default-white;
    }

    form {
      display: none;
    }
  }

  &.is-error {
    .subscribe-form__message {
      display: inline-flex;
      color: $coral-red;
    }

    form {
      display: none;
    }
  }
}
